export function changeValue(num, point) {
    let result = 0;
    if (num > 100000000) {
        result = num / 100000000;
        result = result.toFixed(point) + "";
    } else if (num > 100000) {
        result = num / 100000;
        result = result.toFixed(point) + "";
    } else {
        result = num;
    }
    return result;
}
